@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@font-face{font-family:"SIMPLICIFA";font-weight:900;src:local("SIMPLIFICA"),url(/static/media/SIMPLIFICA.41b3c7c2.ttf) format("truetype")}body{background-color:#000;font-family:SIMPLICIFA,serif;line-height:1.5;margin:0}body::-webkit-scrollbar{width:.8em}body::-webkit-scrollbar-track{box-shadow:inset 0 0 6px rgba(0,0,0,.3)}body::-webkit-scrollbar-thumb{background-color:#a9a9a9;outline:2px solid #708090}#nav{width:100%;top:0;position:fixed;margin:0;text-align:center;padding-top:.25em;padding-bottom:.25em;transition:background .35s linear;cursor:pointer}.scroll{background:#181d27;transition:background .35s linear}#nav li{display:inline;padding:1em 3em}#nav a{transition:color .1s ease-in-out;display:inline-block;position:relative;overflow:hidden;color:#c4e0e2;text-decoration:none;padding-left:.1em;font-size:3rem;letter-spacing:.1em}#nav a:before{content:"";position:absolute;left:0;right:0;bottom:0;background:#2098d1;height:3px;transition-duration:.1s;transition-timing-function:ease-in-out;-webkit-transform:translateY(3px);transform:translateY(3px);transition-property:-webkit-transform;transition-property:transform;transition-property:transform, -webkit-transform}#nav a:hover:before{-webkit-transform:translateY(0);transform:translateY(0)}#nav a:hover{color:#2098d1;transition:color .1s ease-in-out}#mobile-nav{background:#181d27;position:fixed;top:0;-webkit-align-items:center;align-items:center;height:5em;width:100%;font-family:"Open Sans",sans-serif;cursor:pointer}#mobile-nav li{display:inline;padding-left:1em}#mobile-nav a{color:#c4e0e2;text-decoration:none;text-align:center;display:inline-block;font-size:1.2em}ul,li{padding:0;margin:0}
.about{background-color:#fff;font-family:Arial,sans-serif;color:#181d27;font-size:1.5em;padding:5em}.grid-container{display:grid;grid-gap:.3em}.grid-item{background-color:rgba(255,255,255,.8);border:1px solid rgba(0,0,0,.8);text-align:center;background-color:#c4e0e2}.picture{grid-area:1/1/span 1/span 1}.about-desc{grid-area:1/2/span 2/span 4}.contact{grid-area:2/1/span 1/span 1}.css-selector{background:linear-gradient(18deg, #C4E0E2, #181D27);background-size:400% 400%;-webkit-animation:AnimationName 15s ease infinite;animation:AnimationName 15s ease infinite}@-webkit-keyframes AnimationName{0%{background-position:0% 13%}50%{background-position:100% 88%}100%{background-position:0% 13%}}@keyframes AnimationName{0%{background-position:0% 13%}50%{background-position:100% 88%}100%{background-position:0% 13%}}h2{margin:0}.about p{margin:0}
.hero{position:relative;height:100vh;width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;background:radial-gradient(circle, transparent, #181D27 200%);mix-blend-mode:difference}.hero::before{content:"";position:absolute;background-image:url(/static/media/background.199eac09.jpg);background-size:cover;background-position:center bottom;top:0px;right:0px;bottom:0px;left:0px;opacity:.65;overflow:hidden;margin-top:0px}.hero h1{letter-spacing:.15em;position:absolute;color:#c4e0e2;font-size:clamp(2.5rem,10vw,8rem);line-height:.9;text-align:center}.hero p{position:relative;color:#c4e0e2;font-size:clamp(1rem,5vw,2.5rem);line-height:.9;text-align:center;opacity:.85}
